import { React, useState, useEffect } from "react";
import articleContent from "./article-content";
import ArticlesList from "../components/ArticlesList";
import NotFoundPage from "./NotFoundPage";
import CommentsList from "../components/CommentsList";
import UpVotesSection from "../components/UpvotesSection";
import AddCommentForm from "../components/AddCommentForm";
import Fade from "react-reveal/Fade";

const ArticlePage = ({ match }) => {
 const name = match.params.name;
 const article = articleContent.find((article) => article.name === name);
 const [articlesInfo, setArticleInfo] = useState({
  upvotes: 0,
  comments: [],
 });
 // eslint-disable-next-line
 useEffect(() => {
  const fetchData = async () => {
   const result = await fetch(`/api/articles/${name}`);
   const body = await result.json();
   console.log(body);
   setArticleInfo(body);
  };
  fetchData();
 }, [name]);
 if (!article) return <NotFoundPage />;

 const otherArticles = articleContent.filter(
  (article) => article.name !== name
 );
 return (
  <div className="p-5 d-flex flex-column text-left" id="article-page">
   <Fade>
    <div className="p-5 article-content mb-5">
     <h1 className="text-center">{article.title}</h1>
     <UpVotesSection
      articleName={name}
      upvotes={articlesInfo.upvotes}
      setArticleInfo={setArticleInfo}
     />
     <Fade>
      {article.content.map((paragraph, key) => (
       <p key={key}>{paragraph}</p>
      ))}
     </Fade>
    </div>
    <div class="comments-and-other-posts mt-5">
     <Fade>
      <CommentsList comments={articlesInfo.comments} />

      <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
     </Fade>
     <h3>Other Articles</h3>
     <div className="row otherArticles">
      <ArticlesList articles={otherArticles} />
     </div>
    </div>
   </Fade>
  </div>
 );
};
export default ArticlePage;
