import "./App.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import AboutPage from "./pages/AboutPage";
import ArticlePage from "./pages/ArticlePage";
import ArticlesListPage from "./pages/ArticlesListPage";
import NavBar from "./NavBar";
import NotFoundPage from "./pages/NotFoundPage";

import { library } from "@fortawesome/fontawesome-svg-core";
import { faArrowUp, faCoffee } from "@fortawesome/free-solid-svg-icons";

library.add(faArrowUp, faCoffee);
function App() {
 return (
  <Router>
   <div className="App">
    <NavBar />
    <div className="App container d-flex flex-column align-items-center">
     <Switch>
      <Route path="/" component={AboutPage} exact />
      <Route path="/articles-list" component={ArticlesListPage} exact />
      <Route path="/article/:name" component={ArticlePage} exact />
      <Route component={NotFoundPage} />
     </Switch>
    </div>
   </div>
  </Router>
 );
}

export default App;
