import React from "react";
import { Link } from "react-router-dom";

const NavBar = () => (
 <nav
  id="main-nav"
  className="d-flex justify-content-md-end justify-content-center p-2"
 >
  <ul className="col-5 col-md-3 d-flex justify-content-around">
   <li>
    <Link to="/">About</Link>
   </li>
   <li>
    <Link to="/articles-list">Articles</Link>
   </li>
  </ul>
 </nav>
);
export default NavBar;
